@import url(https://fonts.googleapis.com/css?family=Encode+Sans|Lato&display=swap);
*{
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
}
nav {
  width: 100%;
  height: 10vh;
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(25, 26, 31, 0.7);
  z-index: 2;
}
nav div {
  width: 30vw;
  display: flex;
  justify-content: space-between;
}
nav div img {
  width: 35px;
  opacity: 0.9;
}
.block-main {
  position: absolute;
  height: 100vh;
  width: 100%;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
.block-main h1 {
  text-align: center;
  font-size: 4em;
  color: #f1f1f1;
  font-family: 'Encode Sans', sans-serif;
  letter-spacing: 5px;
  z-index: 2;
}
.block-main h2 {
  text-align: center;
  font-size: 1.5em;
  color: #f1f1f1;
  font-family: 'Encode Sans', sans-serif;
  letter-spacing: 5px;
}
.block-main hr {
  margin: 1em 5em;
  border: 2px solid #8743cc;
}
.block-main img {
  width: 30vw;
}
.block-about{
  position: absolute;
  top: 100vh;
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  z-index: 3;
}
.block-about h2 {
  font-size: 2em;
  color: #f1f1f1;
  font-family: 'Encode Sans', sans-serif;
}
.block-about p {
  font-size: 1.5em;
  text-align: center;
  color: #f1f1f1;
  font-family: 'Encode Sans', sans-serif;
  margin: 50px;
  padding: 100px;
  border-radius: 5px;
  background-color: rgba(51, 62, 74, 0.7);
}
.container {
  height: 100vh;
  background-color: #191A1F;
}
